* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  background: #000 url("img/bg.jpeg") no-repeat center center/cover;
  height: 100vh;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  max-width: 1100px;
  margin: auto;
  margin-bottom: 50px;
  padding: 0 20px;
}

.fav-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  color: #fff;
  background-color: #23633f;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin: 10px 0;
  cursor: pointer;
}
.empty-favorite {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
}

.favorite-text {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}
.fav-character {
  color: #fff;
  background-color: #336649;
  font-size: 1em;
  padding: 10px 15px;
  border: 0;
  cursor: pointer;
}
.fav-character-on {
  color: #fff;
  background-color: #336649;
  font-size: 1em;
  margin: auto;
  padding: 10px 15px;
  border: 0;
  cursor: pointer;
}

header {
  height: 200px;
}

.center {
  margin: 30px;
}

.center img {
  width: 200px;
}

.search {
  height: 100px;
  margin-top: 40px;
  padding: auto;
}

input[type="text"] {
  display: block;
  padding: 10px;
  font-size: 20px;
  border: 0;
  border-radius: 5px;
  width: 60%;
  margin: auto;
  outline: none;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  margin: 50px;
  padding: 20px;
}

.card {
  cursor: pointer;
  background-color: transparent;
  height: 300px;
  perspective: 1000px;
}
 
.card h1 {
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card-back {
  background-color: #333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
}

.card li {
  list-style: none;
  padding-bottom: 10px;
}

.social-container {
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
}

.social-container a {
  margin-bottom: 40px;
}
.social-container a i {
  color: white;
  margin: 10px;
}

@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
    margin: 30px;
  }
}

@media (max-width: 500px) {
  .cards {
    grid-template-columns: 1fr;
  }
  .card img {
    width: 100%;
    height: 300px;
    object-fit: contain;
  }
}
